import { defineComponent, ref, watch } from "vue";
import { useStore } from "vuex";
export default defineComponent({
  name: "banners",
  components: {},

  setup() {
    const store = useStore();
    const banners = ref();
    const isShow = ref(true); // 请求轮播图数据

    store.dispatch("recommend/getbanner"); // 监听并拿到轮播图数据

    watch(() => store.state.recommend.banners, value => {
      banners.value = store.state.recommend.banners; // console.log(value.length);

      if (value.length !== 0) {
        isShow.value = false;
      }
    });
    return {
      banners,
      isShow
    };
  }

});